(function(angular) {
  angular.module('sarusApp', [
    'ui.router'
  ]);
})(angular)

(function(module) {
  module.factory('SarusFetch', function($http, COMMON) {
    var SarusFetch = function() {
      this.items = [];
      this.busy = false;
      this.index = 0;
    };

    SarusFetch.prototype.nextPage = function(id) {
      if (this.busy) return;
      this.busy = true;
      var url = COMMON.ServerURL + (id || '');
      $http.get(url).success(function(data) {
        this.items.push(data[0])
        this.busy = false;
        this.index++;
      }.bind(this));
    };

    return SarusFetch;
  });
})(angular.module('sarusApp'));

(function(angular) {
  angular.module('sarusApp').directive('updateUrl', function($window, $state,
    COMMON) {
    var currentState = $state.current;
    var stateName = currentState && currentState.name;
    console.log(currentState);
    return {
      restrict: 'A',
      scope: {
        updateUrl: '@'
      },
      link: function(scope, el, attr) {
        // var lastScrollTop = 0;
        angular.element($window).bind("scroll", function(e) {
          var r = el[0].getBoundingClientRect();
          var top = r.top;
          var elIn = (top >= 0 && top <= $window.innerHeight);
          scope.$apply();
          if (elIn) {
            $state.transitionTo(stateName, {
              q: scope.updateUrl
            }, {
              location: true,
              notify: false,
              reload: false
            })
          }
        });
      }
    };
  });
})(angular);

